import React from 'react'
import styles from './Registration.module.scss'
import { useForm } from "react-hook-form";
import icon_alert from './../components/pics/icon_alert.png'
import icon_chat from './../components/pics/icon_chat.png'
import icon_profile from './../components/pics/icon_profile.png'
import icon_publish from './../components/pics/icon_publish.png'
import { Link, useLocation, useNavigate } from 'react-router-dom'

import { useGlobalContext } from './../redux/actions'


function Registration() {
    const location = useLocation();
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const navigate = useNavigate()
    const { registration } = useGlobalContext()

    function onSubmit(data) {
        registration([data, location.state.data])
        navigate('../login-register')
        // document.getElementById('navbar').style.display = 'flex'
    }


    return (
        <div className={styles.mainBG}>
            <div><  img className={styles.imgpadding} src='https://assets.yad2.co.il/personal/images/general/new_logo_orange.png'></img></div>  {/* navbar img*/}

            <div className={styles.whiteBG}>


                <form className={styles.column} >

                    <label className={styles.title2}>  *</label>
                    <input placeholder='  '  {...register('name')}></input>



                    <label className={styles.title2}>  *</label>
                    <input placeholder='    ' {...register('family')}></input>


                    <label className={styles.title2}>    *</label>
                    <input placeholder='       ' {...register('mobilephone')}></input>

                    <div className={styles.flex}><input className={styles.box} type='checkbox'></input><div className={styles.title5}>    </div></div>

                    <div className={styles.flex}><input className={styles.box} type='checkbox'></input><div className={styles.title5}  >            2    </div></div>



                    <button type='submit' onClick={handleSubmit(onSubmit)} className={styles.btnOrange}></button>


                </form>










            </div>

















            <div className={styles.margin1}  >{/* left div*/}

                <div className={styles.largetitle} > ,  .</div>
                <div className={styles.largetitle} >    .</div>

                <div className={styles.flex}>{/* left div pics*/}
                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_alert}></img>
                        <div className={styles.title2}></div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_chat}></img>
                        <div className={styles.title2}></div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_profile}></img>
                        <div className={styles.title2}> </div>
                    </div>

                    <div className={styles.column}>
                        <img className={styles.imgsize} src={icon_publish}></img>
                        <div className={styles.title2}> </div>
                    </div>

                </div>

            </div>










        </div>
    )
}

export default Registration