import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import styles from './PersonalMessages.module.scss'
import piano from '../pics/piano.svg'

function PersonalMessages() {
    return (
        <div id='messages' >

            <div className={styles.title} > </div>


            <div className={styles.flexbox} >

                <div className={styles.titles}  > :</div>
                <div className={styles.whitebox} >
                    <div className={styles.chevwrapper}  >   <div></div> <FontAwesomeIcon icon={faChevronDown} /></div>
                </div>
                <div className={styles.whitebox} >
                    <div className={styles.chevwrapper}   >   <div></div> <FontAwesomeIcon icon={faChevronDown} /></div>
                </div>
            </div>
            <div className={styles.flexpiano} >

                <div className={styles.pianotext} >

                    <div className={styles.titlelarge} >   </div>

                    <div className={styles.regtext}  >      
                              ?</div>

                    <div className={styles.whitebutton2} >   </div>



                </div>


                <div className={styles.pianoimg}>

                    <img src={piano} ></img>
                </div>



            </div>

        </div>
    )
}

export default PersonalMessages