import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { useGlobalContext } from '../../redux/actions'
import styles from './LastSearches.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faX } from '@fortawesome/free-solid-svg-icons'
function LastSearches() {
    const { loggedName } = useGlobalContext()
    const [data, setdata] = useState([])
    const [counter, setcounter] = useState(1)


    useEffect(() => {

        axios.post(`http://localhost:8080/lastSearchProvide`, { loggedName: loggedName }).then((res) => {
            setdata(res.data)
        })

    }, [counter])




    function date(data) {
        if (data === '0') return ''
        if (data === '1') return ''
        if (data === '2') return ''


    }



    function deleteOneSearch(data) {
        axios.post(`http://localhost:8080/deleteLastSearch`, { postTimeLocal: data }).then((res) => {
            setcounter(counter + res.data)
        })
    }
    function deleteAllSearches() {
        axios.post(`http://localhost:8080/deleteAllSearches`, { loggedName: loggedName }).then((res) => {
            setcounter(counter + res.data)
        })
    }




    return (
        <div className={styles.wrapper}>
            <div className={styles.wrapper1}>
                <div className={styles.toptitle} >
                    <div className={styles.largetitle} > </div>
                    <div className={styles.greytitle} onClick={() => deleteAllSearches(data.loggedName)} > </div>
                </div>
                {data.map((d) => <div className={styles.flexrow}>
                    <div className={styles.titledate}  > {`${d.postTimeDate} ${date(d.postTimeMonth)} `}  </div>

                    <div className={styles.whitebox} >
                        <div className={styles.topline}  >  <div className={styles.title} >"- </div>
                            <FontAwesomeIcon onClick={() => deleteOneSearch(d.postTimeLocal)} className={styles.xmargin} icon={faX} /></div>
                        <div className={styles.flex}>


                            <div className={styles.variable} > {d.city != 0 ? ':' : ''} </div> <div className={styles.values} >{d.city != 0 ? d.city : ''}</div>
                            <div className={d.city != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.kind != 0 ? ':' : ''}</div> <div className={styles.values} >{d.kind != 0 ? d.kind : ''}</div>
                            <div className={d.kind != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {(d.roomsFrom != 0 && d.roomsTo != 0) ? ':' : ''}
                            </div> <div className={styles.values} >{(d.roomsFrom != 0 && d.roomsTo != 0) ? `${d.roomsTo} - ${d.roomsFrom}` : ''}</div>
                            <div className={(d.roomsFrom != 0 && d.roomsTo != 0) ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {(d.pricefrom != 0 && d.priceto != 0) ? ':' : ''}
                            </div> <div className={styles.values} >{(d.pricefrom != 0 && d.priceto != 0) ? `${d.priceto} - ${d.pricefrom}` : ''}</div>
                            <div className={(d.pricefrom != 0 && d.priceto != 0) ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.desription != 0 ? ' :' : ''}</div> <div className={styles.values} >{d.desription != 0 ? d.desription : ''}</div>
                            <div className={d.desription != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {(d.sizeFrom != 0 && d.sizeTo != 0) ? '":' : ''}
                            </div> <div className={styles.values} >{(d.sizeFrom != 0 && d.sizeTo != 0) ? `${d.sizeTo} - ${d.sizeFrom}` : ''}</div>
                            <div className={(d.sizeFrom != 0 && d.sizeTo != 0) ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {(d.floorFrom != 0 && d.floorTo != 0) ? ':' : ''}
                            </div> <div className={styles.values} >{(d.floorFrom != 0 && d.floorTo != 0) ? `${d.floorTo} - ${d.floorFrom}` : ''}</div>
                            <div className={(d.floorFrom != 0 && d.floorTo != 0) ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.balcony != 0 ? ':' : ''}</div> <div className={styles.values} >{d.balcony != 0 ? d.balcony : ''}</div>
                            <div className={d.balcony != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.bars != 0 ? ':' : ''}</div> <div className={styles.values} >{d.bars != 0 ? d.bars : ''}</div>
                            <div className={d.bars != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.diabled != 0 ? '  :' : ''}</div> <div className={styles.values} >{d.diabled != 0 ? d.diabled : ''}</div>
                            <div className={d.diabled != 0 ? styles.bar : styles.nothing} ></div>

                            <div className={styles.variable} > {d.elevator != 0 ? ':' : ''}</div> <div className={styles.values} >{d.elevator != 0 ? d.elevator : ''}</div>
                            <div className={d.elevator != 0 ? styles.bar : styles.nothing} ></div>

                        </div>

                        <div className={styles.time} >{d.postTimeLocal}</div>

                    </div>

                </div>)}


            </div>

        </div>
    )
}

export default LastSearches